import Vue from 'vue';
import Vuex from 'vuex';

Vue.use (Vuex);

import actions from './actions.js';
import getters from './gettings.js';
import seller from './elememodel/seller.js';
import goods from './elememodel/goods.js';
import ratings from './elememodel/ratings.js';

export default new Vuex.Store({
    actions,
    getters,
    modules:{
        seller,
        goods,
        ratings
    }
})


