<template>
  <div class="goods">
      <div class="left" ref="hh">
          <!-- {{currentIndex}} -->
        <ul>
            <li v-for='(item,i) in goods' :key='i' @click='selectMenu(i)'
           :class = "{'current' : i === currentIndex}">
                <p class="text" >{{item.name}}</p>
            </li>
        </ul>
      </div>
      <div class="right" ref="dd">
          <div>
            <div class="cofound food-list-hook"  v-for='(item,i) in goods' :key='i'>
                <p>{{item.name}}</p>
                <div  class="cofPhoto" v-for='(food,j) in item.foods' :key='j'>
                    <!-- <img :src="it.icon" @click='showFood(food)'> -->
                    <img :src="food.icon" @click='showFood(food)'>
                    <p class="txt1">{{food.name}}</p>
                    <p class="txt2" v-if="food.description">{{food.description}}</p>
                    <p class="txt3">月售{{food.sellCount}}份 好评率{{food.rating}}%</p>
                    <p class="txt4">￥{{food.price}}</p>
                    <p class="txt5" v-if='food.oldPrice'><del>{{food.oldPrice}}</del></p>
                    <p>
                        <icon></icon>
                    </p>
                </div>
            </div>
          </div> 
      </div>
    <foodDetail></foodDetail>
  </div>
 
</template>

<script>
    import BScroll from 'better-scroll';
    import icon from './icon.vue';
    import { mapGetters , mapActions } from 'vuex';
    import foodDetail from '../foodDetail/foodDetail.vue';
    export default {
        data(){
			return {
				listHeight : [],
                scrollY:0
			}
        },
        
        components : {
            icon,
            foodDetail
        },
        created(){
            this.$store.dispatch('getGoods');
        },
        watch:{
            goods(){
                this.$nextTick(()=>{
                    this.hmenuBS();
                    this.dmenuBs();
                });
            }
        },
        computed:{
            ...mapGetters([
                'goods'
            ]),
            // 计算当前索引值
			currentIndex(){
				// 循环listHeight数组
				for( let i = 0 ; i < this.listHeight.length ; i++ ){
					let height1 = this.listHeight[i];		// 0
					let height2 = this.listHeight[i+1];		// 1434
					// 根据滚动的y轴坐标值 与listHeight数组区间进行比较 
					// 0-100 101-200
					if( this.scrollY >= height1 && this.scrollY  < height2 ){
						return i;
					}
					// if( this.scrollY < this.listHeight[i+1] - 1 ){
					// 	return i;
					// }
				}
				return 0 ;
			},
        },
        methods:{
            hmenuBS(){
                let menuBScroll = new BScroll( this.$refs.hh , {click:true} );
            },
            dmenuBs(){
                let menuBScroll = new BScroll( this.$refs.dd , {
                    click:true,
                    probeType:3
                } );
                // 实时获取y坐标
				menuBScroll.on('scroll' , (pos) => {
					// console.log(pos.y);
					// console.log( Math.abs( Math.ceil(pos.y)));
					this.scrollY =  Math.abs(Math.round(pos.y)) + 2;
				})
            },
            // 计算li的高度 并且push进listHeight数组当中
			calcHeight(){
				// 获取所有li
				let foodList = this.$refs.dd.getElementsByClassName('food-list-hook');
				// 初始值为0
				let height = 0;
				this.listHeight.push( height );
				// 循环所有li
				for( let i = 0 ; i < foodList.length ; i++){
					height +=  foodList[i].clientHeight;
					this.listHeight.push( height );
				}
				// console.log( this.listHeight );
            },
            // 左侧点击事件
			selectMenu(i){
				console.log(i);
				let foodList = this.$refs.dd.getElementsByClassName('food-list-hook');
				let el = foodList[i];
				let menuBScroll = new BScroll( this.$refs.dd , {
                    click:true,
                    probeType:3
                } );
				menuBScroll.scrollToElement( el , 200 );
			},

            showFood(food){
                this.$store.dispatch('showFood',food);
            }
            
        }
    }
</script>

<style scoped lang="less">
   .goods{
		position: absolute;
		display: flex;
        width: 100%;
        height:100%;
		top:3.52rem;
        bottom: 0.9rem;
        z-index: 1;
		.left{
			flex: 0 0 1.6rem;//不扩展 不缩放
            width: 1.6rem;
            height:8.8rem;
            background: #f3f5f7; 
            overflow: hidden;
            z-index: -1;
            ul{
                width:1.12rem;
                padding: 0  0.24rem;
                li{
                    width:1.12rem;
                    height: 1.08rem;
                    display:table;
                    font-size: 0.24rem;
                    font-weight: 200;
                    line-height: 0.28rem;
                    border-bottom: 1px solid #dbdee1;
                    &.current{
					    color:red;
				    }
                    p{
                        display: table-cell;  
                        vertical-align: middle;
                    }
                }
            }
		}
		.right{
            flex:1;
            overflow: hidden;
            height:8.8rem;
            .cofound{
                width:100%;
                background:#f3f5f7;
                border-left:0.04rem solid #d9dde1;
                p{  
                    height:0.52rem;
                    position:relative;
                    padding-left:0.28rem;
                    line-height: 0.52rem;
                    color:rgb(147,153,159);
                    font-size: 0.24rem;
                    font-weight: 700;
                }
            }
            .cofPhoto{     
                width:100%;
                height:1.94rem;
                background-color:rgb(255,255,255); 
                border-bottom: 0.01rem solid #d9dde1;
                img{
                    width:1.18rem;
                    height: 1.18rem;
                    padding-top:0.36rem;
                    padding-bottom:0.36rem;
                    padding-left:0.36rem;
                    // border:1px solid red;
                }
                .txt1{
                    margin-left:1.4rem;
                    margin-top:-1.56rem;
                    font-size:0.28rem;
                    color:rgb(7,17,27);
                    line-height:0.28rem;
                }
                .txt2{
                    margin-left:1.4rem;
                    margin-top:-0.1rem;
                    font-size:0.2rem;
                    color:rgb(147,153,159);
                    line-height:0.2rem;
                }
                .txt3{
                    margin-left:1.4rem;
                    margin-top:-0.1rem;
                    font-size:0.2rem;
                    color:rgb(147,153,159);
                    line-height:0.2rem;
                }
                .txt4{
                    margin-left:1.4rem;
                    margin-top:-0.25rem;
                    font-size:0.1rem;
                    color:rgb(240,20,20);
                    line-height:0.48rem;
                    font-weight:700;
                }
                .txt5{
                    margin-left:2rem;
                    margin-top:-0.5rem;
                    font-size:0.2rem;
                    color:rgb(147,153,159);
                    line-height:0.48rem;
                    font-weight:700;
                }
             }
        }
	}
	
</style>




