<template>
    <transition enter-action-class="animated bounceInLeft"
        leave-active-class="animated bounceOutRight">
        <div class='foods'  @click='hideFood'  v-show='foodshow'>
           
            <div class='imgshow'>
                <img :src="food.image">
                <!-- {{food}} -->
            </div> 
            <div class='textshow' ref="xx">
                <div>
                    <div class="define1">
                        <p class="pidan1">{{food.name}}</p>
                        <p class="pidan2">月售{{food.sellCount}}份  好评率{{food.rating}}％</p>
                        <p class="pidan3" v-if='food.price'>¥{{food.price}}</p>
                        <del class="pidan4" v-if='food.oldPrice'>¥{{food.oldPrice}}</del>
                        <p class="pidan5"><span>加入购物车</span></p>
                        <div class="pidan6"></div>
                    </div>
                    <div class="define2">
                        <p class="blance1">商品介绍</p>
                        <p class="blance2">{{food.info}}</p>
                        <div class="blance3"></div>
                    </div>
                    <div class="define3">
                        <div class="faith1">
                            <p class="comment1">商品评价</p>
                            <div class="comment2">
                               <p class="comt1">全部 <span>57</span></p>
                               <p class="comt2">推荐 <span>47</span></p> 
                               <p class="comt3">吐槽 <span>10</span></p>
                            </div>
                            <p class="comment3">
                                <i class="iconfont">&#xe608; <span> 只看有内容的评价</span></i>
                            </p>
                        </div>
                        <div class="faith2" v-for='(user,i) in food.ratings' :key="i">
                            <div class="up">
                                <p class="up1">{{user.rateTime}}</p>
                                <img :src="user.avatar">
                                <p class="up2">{{user.username}}</p>
                                
                            </div>
                            <div class="below">
                                <i class="iconfont below1" v-if="user.rateType==0">&#xe503;</i>
                                <i class="iconfont below2" v-if="user.rateType==1">&#xe502;</i>
                                <p class="below3">{{user.text}}</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </transition>
</template>
<script>
    import { mapGetters } from 'vuex';
    import BScroll from 'better-scroll';
    import animate from 'animate.css';
    export default {
        computed:{
            ...mapGetters([
                'foodshow',
               ' hideFood',
               'food'
            ])
        },
        watch:{
            food(){
                this.$nextTick(()=>{
                    this.pmenuBs();
                });
            }
        },
        methods:{
            hideFood(){
                // console.log(2);
                this.$store.dispatch('hideFood');
            },
            pmenuBs(){
                 let menuBScroll = new BScroll( this.$refs.xx , {click:true} );
            }
        }
    }
</script>
<style lang='less' scoped>
    .foods {
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
        background: rgba(7,17,27,0.8);
        z-index:98;
		.imgshow{
			position: relative;
			width: 100%;
			height: 0;
			padding-top: 100%;
			img{
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
                height: 100%;
            }
        }
        .textshow{
            position: absolute;
            width:100%;
            height:4.75rem;
            overflow: hidden;
            // border:1px solid red;
            .define1{
                width:100%;
                height: 2.18rem;
                background:rgb(255,255,255);
                .pidan1{
                    font-size:0.28rem;
                    font-weight:700;
                    color:rgb(7,17,27);
                    line-height:0.28rem;
                    padding:0.36rem 0 0 0.36rem;
                }
                .pidan2{
                    font-size:0.2rem;
                    color:rgb(147,153,153);
                    line-height:0.2rem;
                    padding:0.16rem 0 0 0.36rem;
                }
                .pidan3{
                    font-size:0.28rem;
                    font-weight:700;
                    color:rgb(240,20,20);
                    line-height:0.48rem;
                    padding:0.36rem 0 0 0.46rem;
                }
                .pidan4{
                    float: left;
                    font-size:0.2rem;
                    font-weight:700;
                    color:rgb(147,153,159);
                    line-height:0.48rem;
                    padding-left:0.96rem;
                    margin-top:-0.46rem;
                }
                .pidan5{
                    float: right;
                    width: 1.48rem;
                    height: 0.48rem;
                    margin-top:-0.36rem;
                    margin-right:0.36rem;
                    background:rgb(0,160,220); 
                    border-top-left-radius: 0.24rem;
                    border-top-right-radius: 0.24rem;
                    border-bottom-left-radius: 0.24rem;
                    border-bottom-right-radius: 0.24rem; 
                    span{
                        display: inline-block;
                        width: 1.48rem;
                        height: 0.48rem;
                        text-align: center;
                        line-height: 0.48rem;
                        font-size: 0.2rem;
                        color:rgb(255,255,255);
                    }
                }
                .pidan6{
                    margin-top:0.34rem;
                    height: 0.5rem;
                    background-color: rgb(243,245,247);
                    // border:1px solid red;
                }
            }
            .define2{
                width:100%;
                height: 2.5rem;
                background:rgb(255,255,255);
                margin-top:0.47rem;
                .blance1{
                    padding:0.36rem 0 0 0.36rem;
                    font-size:0.28rem;
                    font-weight:700;
                    color:rgb(44,50,56);
                }
                .blance2{
                    padding:0.12rem 0.36rem 0.3rem 0.52rem;
                    font-size:0.24rem;
                    font-weight:200;
                    line-height:0.48rem;
                    color:rgb(138,142,147);
                }
                .blance3{
                    height: 0.5rem;
                    background-color: rgb(243,245,247);
                    // border:1px solid red;
                }
            }
            .define3{
                width:100%;
                height: 7rem;
                background:rgb(255,255,255);
                margin-top:0.48rem;
                // border:1px solid red;
                .faith1{
                    height: 3rem;
                    border-bottom:1px solid #e6e7e8;
                    .comment1{
                        padding:0.36rem 0 0 0.36rem;
                        font-size:0.28rem;
                        font-weight:700;
                        color:rgb(44,50,56);
                    }
                    .comment2{
                        padding:0.36rem 0 0.36rem 0.2rem;
                        border-bottom:1px solid #e6e7e8;
                        display: flex;
                        p{
                            width:1.2rem;
                            height: 0.62rem;
                            justify-content: space-between;
                            margin-left:0.16rem;
                            border-radius: 0.06rem;
                        }
                        .comt1{
                            background-color: rgb(0,160,220);
                            height: 0.62rem;
                            line-height: 0.62rem;
                            text-align: center;
                            font-size: 0.26rem;
                            color:rgb(255,255,255);
                            span{
                                display:inline-block;
                                font-size: 0.01rem;
                            }
                        }
                        .comt2{
                            background-color: rgb(204,236,248);
                            height: 0.62rem;
                            line-height: 0.62rem;
                            text-align: center;
                            font-size: 0.26rem;
                            color:rgb(77,85,93);
                            span{
                                display:inline-block;
                                font-size: 0.01rem;
                            }
                        }
                        .comt3{
                            background-color: rgb(233,235,236);
                            height: 0.62rem;
                            line-height: 0.62rem;
                            text-align: center;
                            font-size: 0.26rem;
                            color:rgb(77,85,93);
                            span{
                                display:inline-block;
                                font-size: 0.01rem;
                            }
                        }
                    }
                    .comment3{
                        width:100%;
                        height:1rem;
                        i{
                            -webkit-font-smoothing: antialiased;
                            -moz-osx-font-smoothing: grayscale;
                            display:inline-block;
                            color:rgb(183,187,191);
                            font-size: 0.4rem;
                            padding:0.3rem 0 0.3rem 0.48rem;
                            span{
                            display:inline-block;
                            font-size: 0.24rem;
                            color:rgb(180,184,187);
                            }
                        }
                    }
                }
                .faith2{
                    height: 1.34rem;
                    width:100%;
                    // border:1px solid red;
                    border-bottom:1px solid #e6e7e8;
                    .up{
                        .up1{
                            padding:0.32rem 0 0 0.36rem;
                            font-size: 0.2rem;
                        }
                        .up2{
                            float:right;
                            padding-right:0.62rem;
                            margin-top:-0.2rem;
                        }
                        img{
                            float:right;
                            margin-right:0.32rem;
                            margin-top:-0.25rem;
                            width:0.24rem;
                            height:0.24rem;
                            border-radius: 0.12rem;
                        }
                    }
                    .below{
                        padding:0.32rem 0 0 0.36rem;
                        .below1{
                            float:left;
                            font-size: 0.24rem;
                            color:rgb(147,153,159);
                        }
                        .below2{
                            float:left;
                            font-size: 0.24rem;
                            color:rgb(0,160,220);
                        }
                        .below3{
                            float:left;
                            font-size: 0.24rem;
                            color:rgb(7,17,27);
                            padding:0 0 0 0.08rem;
                        }
                    }
                }
            } 
        }  
	}  
</style>





