<template>
    <transition enter-action-class="animated bounceInLeft"
    leave-active-class="animated bounceOutRight">
        <div class="detail" v-show="detailShow">
            <p @click='hideDetail'>{{seller.name}}</p>
            <star :score="seller.score" :size="'big'" class="star"></star>
            <div class="title">
                <p>优惠信息</p>
                <p></p>
                <p></p>
            </div>
        </div>
        
    </transition>
</template>

<script>
    import { mapGetters }from 'Vuex';
    import animate from 'animate.css';
    import star from '../star/star.vue';
    export default {
        props:['name'],
        components:{
            star
        },
        computed:{
            ...mapGetters([
                'seller',
                'detailShow'
            ])
        },
        methods:{
            hideDetail(){
                this.$store.dispatch('hideDetail');
            }
        }
    }
</script>

<style lang='less' scoped>
    .detail{
        position: absolute;
		font-size: 0.4rem;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
        background:rgba(7,17,27,0.8);
        z-index: 999;
        p{
            width:100%;
            position:absolute;
            float: left;
            padding-top:1.28rem;
            text-align: center;
            color:rgb(255,255,255);
        }
        .star{
            width:100%;
            text-align: center;
            padding-top:1.6rem;
        }
   
	}
</style>




