import types from '../types.js';
import axios from 'axios';

var state = {
    goods : [],
    food:{},
    foodshow: false
};

var getters = {
    goods( state ){
        return state.goods;
    },
    food( state ){
		return state.food;
	},
    foodshow(){
        return state.foodshow;
    }
};

var actions = {
    getGoods( { commit,state }){
        axios.get('./data.json').then( (res)=>{ 
        // axios.get('/api/goods').then( (res)=>{ 
            // console.log(res);
            // if( res.data.errno === 0 ){
                commit( types.GET_GOODS , res.data.goods);
                // commit( types.GET_GOODS , res.data.data);
            // }
        })
    },
    showFood( { commit,state } ,food ){
		// console.log(4);
		commit ( types.showFood ,food);
	},
	hideFood( { commit } ){
		commit ( types.hideFood );
	}

};

var mutations = {
    [types.GET_GOODS]( state , data ){
        state.goods = data;
    },
    [types.showFood]( state , food){
        state.foodshow = true;
        state.food = food;
    },
    [types.hideFood]( state ){
		state.foodshow = false;
	}

}

export default {
    state,
    getters,
    actions,
    mutations
}


