      <div class="assess1">
        <div class="asse1">
          <p class="grade1">{{seller.rankRate}}</p>
          <p class="grade2">综合评分</p>
          <p class="grade3">高于周边商家69.2%</p>
        </div>
        <div class="asse2">
          <p class="serve1">服务态度 <span><star :score="seller.serviceScore" ></star></span> <span>{{seller.serviceScore}}</span></p>
          <p class="serve2">服务态度 <span><star :score="seller.foodScore" ></star></span> <span>{{seller.foodScore}}</span></p>
          <p class="serve3">送达时间 <span v-if="ratings.deliveryTime">{{ratings.deliveryTime}}分钟</span></p>
        </div>
        <div class="asse3"></div>
      </div>
      <div class="assess2">
        <div class="half1">
          <p class="total">全部 <span>57</span></p>
          <p class="satisfy">满意 <span>47</span></p>
          <p class="unsatisfy">不满意 <span>10</span></p>
        </div>
        <div class="half2">
          <i class="iconfont">&#xe608; <span> 只看有内容的评价</span></i>
        </div>
        
      </div>
      <div class="assess3" v-for="(grade,i) in ratings" :key="i">
        <div class="appraise1">
          <!-- <p class="app1">{{grade.avatar}}</p> -->
          <img :src="grade.avatar">
          <p class="app2">{{grade.username}}</p>
          <p class="app3">{{grade.rateTime}}</p>
          <p class="app4"><star :score="seller.score" ></star></p>
        </div>
        <div class="appraise2">
            <p>{{grade.text}}</p>
            <i class="iconfont app4" v-if="grade.rateType==0">&#xe503;</i>
            <i class="iconfont app5" v-if="grade.rateType==1">&#xe502;</i>
        </div>
      </div>
    </div>
      
  </div>
</template>

<script>
    import BScroll from 'better-scroll';
    import { mapGetters } from 'vuex';
    import star from '../star/star.vue';
    export default {
         components:{
            star
      },
      computed:{
          ...mapGetters([
              'ratings',
              'seller'
        ]),
       },    
      created(){
        this.$store.dispatch('getRatings');
      } ,
      watch:{
            goods(){
                this.$nextTick(()=>{
                    this.kmenuBS();
                });
            }
        },
        methods:{
            kmenuBS(){
                let menuBScroll = new BScroll( this.$refs.kk , {click:true} );
            },  
        }
    }
</script>

