import types from '../types';
import axios from 'axios';

var state = {
    seller : {},
    detailShow : false,
}

var getters = {
    seller(state){
        return state.seller;
    },
    detailShow( state ){
		return state.detailShow;
	}
}

var actions = {
    getSeller({commit,state}){
        axios.get('./data.json').then((res)=>{
        // axios.get('/api/seller').then((res)=>{
            // if( res.data.errno === 0){
                commit( types.GET_SELLER ,res.data.seller);
                // commit( types.GET_SELLER ,res.data.data);
            // }
        });
    },
    showDetail( { commit } ){
		// console.log(4);
		commit ( types.showDetail );
	},
	hideDetail( { commit } ){
		commit ( types.hideDetail );
	}
}

var mutations = {
    [types.GET_SELLER](state , data){
        state.seller = data;
    },
    [types.showDetail]( state ){
		state.detailShow = true;
    },
    [types.hideDetail]( state ){
		state.detailShow = false;
	}
}

export default {
    state,
    getters,
    actions,
    mutations
}


