<template>
  <div class="business" ref="hh">
    <div>
      <div class="business1" >
        <div class="information1">
          <div class="start1">
            <p class="sel1">{{seller.name}}</p>
            <p class="sel2"><star :score="seller.score" class="star"></star></p>
            <p class="sel3">(661)&nbsp;&nbsp;月售690单</p>
          </div>
          <div class="start2">
            <i class="iconfont sel4">&#xe687;</i>
            <p class="sel5">已收藏</p>
          </div>
        </div>
        <div class="information2">
           <div class="carry1">
             <p class="deliver1">起送价</p>
             <p class="deliver2">{{seller.minPrice}}<span>元</span></p>
           </div>
           <div class="carry2">
             <p class="deliver1">商家配送</p>
             <p class="deliver2">{{seller.deliveryPrice}}<span>元</span></p>
           </div>
           <div class="carry3">
             <p class="deliver1">平均配送时间</p>
             <p class="deliver2">{{seller.deliveryTime}}<span>分钟</span></p>
           </div>
        </div>
        <div class="information3"></div>
      </div>
      <div class="business2">
        <p class="public1">公告与活动</p>
        <p class="public2">{{seller.bulletin}}</p>
        <div class="decrease">
          <p class="desc1"><img src="../../assets/imgs/decrease.png"></p>
          <p v-if='seller.supports' class="desc2">{{seller.supports[0].description}}</p>
        </div>
        <div class="decrease">
          <p class="desc1"><img src="../../assets/imgs/discount.png"></p>
          <p v-if='seller.supports' class="desc2">{{seller.supports[2].description}}</p>
        </div>
        <div class="decrease">
          <p class="desc1"><img src="../../assets/imgs/discount.png"></p>
          <p v-if='seller.supports' class="desc2">{{seller.supports[1].description}}</p>
        </div>
        <div class="decrease">
          <p class="desc1"><img src="../../assets/imgs/special.png"></p>
          <p v-if='seller.supports' class="desc2">{{seller.supports[0].description}}</p>
        </div>
        <div class="decrease">
          <p class="desc1"><img src="../../assets/imgs/special.png"></p>
          <p v-if='seller.supports' class="desc2">{{seller.supports[2].description}}</p>
        </div>
        <div class="decrease">
          <p class="desc1"><img src="../../assets/imgs/invoice.png"></p>
          <p v-if='seller.supports' class="desc2">{{seller.supports[3].description}}</p>
        </div>
        <div class="decrease">
          <p class="desc1"><img src="../../assets/imgs/guarantee.png"></p>
          <p v-if='seller.supports' class="desc2">{{seller.supports[4].description}}</p>
        </div>
        <div class="decrease1"></div>
      </div>
      <p class="shop1">商家实景</p>
      <div class="business3" ref='menuWrap'>
        

        <div class="shop2" ref='picList'>
            <div class="pic-item" v-for='(item,i) in seller.pics' :key="i">
              <img :src="item">
            </div> 
        </div>
        <div class='shopping'></div>
      </div>
      <div class="business4">
        <span>商家信息</span>
        <p v-if='seller.infos'>{{seller.infos[0]}}</p>
        <p v-if='seller.infos'>{{seller.infos[1]}}</p>
        <p v-if='seller.infos'>{{seller.infos[2]}}</p>
        <p v-if='seller.infos'>{{seller.infos[3]}}</p>
      </div>
    </div>
  </div>
</template>

<script>
    import BScroll from 'better-scroll';
    import { mapGetters } from 'vuex';
    import star from '../star/star.vue';
    export default {
      components:{
            star
      },
      computed:{
          ...mapGetters([
              'seller'
          ]),
              
      },
      watch:{
          seller(){
              this.$nextTick(()=>{
                  this.hmenuBS();
              });
              let picWidth = 120;
	          	let margin = 6;
	          	let width = (picWidth + margin) * this.seller.pics.length - margin;
	          	this.$refs.picList.style.width = width + 'px';

              if( !this.picBScroll ){
                this.picBScroll = new BScroll( this.$refs.menuWrap , {
                  scrollX: true,
                })
              }else{
                this.picBScroll.refresh();
              }
          }
      },
      methods:{
        hmenuBS(){
            let menuBScroll = new BScroll( this.$refs.hh , {click:true} );
        }
      },
    }

</script>

