            display:inline-block;
            margin-top:0.36rem;
            margin-right:0.36rem;
          }
          .sel5{
            height:0.48rem;
            color:rgb(77,85,93);
            font-size: 0.2rem;
            line-height: 0.2rem;
            margin-top:0.08rem;
            margin-right:0.28rem;
          }
        }
        .information2{
          width:100%;
          height:1.5rem;
          display:flex;
          .carry1{
            width:2.5rem;
            height:1.5rem;
            // border:1px solid red;
            border-right:0.01rem solid #e9ebec;
            .deliver1{
              color:rgb(147,153,159);
              font-size: 0.2rem;
              line-height: 0.2rem;
              padding:0.36rem 0 0 0.92rem;
            }
            .deliver2{
              color:rgb(7,17,27);
              font-size: 0.48rem;
              line-height: 0.48rem;
              padding:0.1rem 0 0 0.92rem;
              span{
                font-size: 0.2rem;
                color:rgb(147,153,159);
              }
            }
          }
          .carry2{
            width:2.5rem;
            height:1.5rem;
            // border:1px solid red;
            border-right:0.01rem solid #e9ebec;
            .deliver1{
              color:rgb(147,153,159);
              font-size: 0.2rem;
              line-height: 0.2rem;
              padding:0.36rem 0 0 0.7rem;
            }
            .deliver2{
              color:rgb(7,17,27);
              font-size: 0.48rem;
              line-height: 0.48rem;
              padding:0.1rem 0 0 0.92rem;
              span{
                font-size: 0.2rem;
                color:rgb(147,153,159);
              }
            }
          }
          .carry3{
            width:2.5rem;
            height:1.5rem;
            // border:1px solid red;
            .deliver1{
