        height:10.3rem;
        .public1{
          padding:0.36rem 0 0 0.36rem;
          font-size: 0.28rem;
        }
        .public2{
          padding:0.16rem 0.24rem 0.32rem 0.6rem;
          color:rgb(240,20,20);
          font-size: 0.24rem;
          font-weight: 200;
          line-height: 0.48rem;
          border-bottom: 0.01rem solid #e9ebec;
        }
        .decrease{
          height:0.96rem;
          // border:1px solid red;
          border-bottom: 0.01rem solid #e9ebec;
          display:flex;
          .desc1{
