<template>
  <div id="app">
    <!-- 占位符 -->
    <v-header></v-header>
    
    <detail></detail>
    <div class="nav">
      <div class="product">
        <router-link to="/goods">商品</router-link>
      </div>
      <div class="product">
        <router-link to="/ratings">评价</router-link>
      </div>
      <div class="product">
        <router-link to="/seller">商家</router-link>
      </div> 
    </div> 
    <div class="down">
        <div class="down1">
            <i class="iconfont circle1">&#xf0082;</i>
            <p class="circle2"></p>
            <p class="circle3">¥0</p>
        </div>
        <div class="down2">
            <p>另需配送费¥{{seller.deliveryPrice}}</p>
        </div>
        <div class="down3">
            <p>¥{{seller.minPrice}}起送</p>
        </div>
    </div>
<!-- 在组建中项目定义后可引入这里，变成在所有界面显示 -->
    <!-- 占位符 -->
    <router-view></router-view>

  </div>
</template>

<script>
  import navs from './components/header/navs.vue';
  import detail from './components/detail/detail.vue';
  import { mapGetters } from 'vuex';
  import foodDetail from './components/foodDetail/foodDetail.vue';
  export default {
    components : {
      'v-header' : navs,
      detail,
      foodDetail

    },
    computed:{
          ...mapGetters([
              'ratings',
              'seller'
        ]),
    },    
    created(){
      this.$store.dispatch('getSeller');
      // this.$store.dispatch('getGoods');
    }
  }
</script>

<style lang="less" scoped>
  #app {
    // font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    // -moz-osx-font-smoothing: grayscale;
    // font-size: 0.3rem;
    // color: #2c3e50;
    .nav{
      position: absolute;
      z-index: 1;
      width: 100%; 
      height: 0.8rem;
      line-height: 0.8rem;
      font-size: 0.28rem;
      color: rgb(77,85,93);
      border-bottom: 1px solid rgba(7,17,27,0.1);
      display: flex;
      margin-top:2.68rem;
      .product{
        flex:1;
        text-align: center;
        a{
          display: block;
          &.router-link-active{
            color:red;
          }
        }
      }
    }
    .down{
      position: absolute;
      height:1.04rem;
      width: 100%;
      bottom:0;
      z-index: 99;
      overflow: hidden;
      
      .down1{
          position: absolute;
          width:2.48rem;
          height:1.04rem;
          background-color: #131d26; 
          .circle1{
            position: relative;
            font-size: 0.48rem;
            z-index: 98;
            left:0.6rem;
            top:0.1rem;
            color:rgba(255,255,255,0.4);
          }
          .circle2{
            position: relative;
            width:1.04rem;
            height:1.04rem;
            background-color:#2b343c;
            left:0.36rem;
            top:-0.66rem;
            border-radius: 1.04rem;
          }
          .circle3{
            position: absolute;
            color:red;
            height:1.04rem;
            bottom:0;
            left:1.6rem;
            top:0.25rem;
            line-height:0.48rem;
            font-size: 0.48rem;
            color:rgba(255,255,255,0.4rem);

          }
      }
      .down2{
          position: absolute;
          width:2.93rem;
          height:1.04rem;
          left:2.48rem;
          background-color: #141d27; 
          border:1px solid #2c343d;
          p{
            height:1.04rem;
            line-height: 1.04rem;
            padding-left:0.24rem;
            font-size: 0.32rem;
            font-weight:700;
            color:rgba(255,255,255,0.4);
          }
      }
      .down3{
          position: absolute;
          width:2.1rem;
          height:1.04rem;
          left:5.4rem;
          background-color: #2b333b;
          p{
            height:1.04rem;
            line-height: 1.04rem;
            text-align: center;
            font-size: 0.32rem;
            font-weight:700;
            color:rgba(255,255,255,0.4);
          } 
      }
    } 
  }
</style>



