<template>
    <div class="top">
        <div class="topPhoto1">
            <img :src="seller.avatar">
        </div>
        <p>{{seller.name}}</p>
        <div class="topPhoto2">
            <img :src="seller.avatar"  @click = 'showDetail'>
        </div>
        <div class="topPhoto3">
            <img src="../../assets/imgs/brand.png">
        </div>
        <div class="text">
            <p>蜂鸟专送／38分钟送达</p>
            <img src="../../assets/imgs/decrease.png">
            <p class="txt" v-if='seller.supports'>{{seller.supports[0].description}}</p>
        </div>
        <div class="topPhoto4">
           <p class="five">5个 ></p>
        </div>
        <div class="topPhoto5">
            <img src="../../assets/imgs/bulletin.png">
            <p>粥品香坊其烹饪粥料的秘方源于中国千年古法,再融合现代制作工艺... ></p>
        </div>
    </div>
</template>

<script>
    import { mapGetters , mapActions } from 'vuex';
    export default {
        components:{

		},
        computed:{
            ...mapGetters([
                'seller'
            ])
        },
        methods:{
			...mapActions([
				'showDetail'
			])
		}
    }
</script>

<style scoped lang="less">
    .top {
        width: 100%;
        height: 2.12rem;
        position: absolute;
        // background-color:rgba(7,17,27,0.5);
        
        p{  
            position: absolute;
            color:rgb(255,255,255);
            font-size: 0.32rem;
            font-weight:bold;
            line-height: 0.36rem;
            padding-left:3rem;
            padding-top:0.54rem;   
        }
        .topPhoto1{
            position: absolute; 
            top:0;
            left: 0;
            right:0;
            bottom: 0;
            filter:blur(10px);
            img{
                width:100%;
                height:2.12rem;
            }
        }
        .topPhoto2{
            img{ 
                position: absolute;
                width:1.28rem;
                height:1.28rem;
                padding-left:0.48rem;
                padding-top: 0.48rem;
                border-radius: 0.04rem;
                z-index: 1;
            }
        }
        .topPhoto3{
            position: absolute;
            padding-left:2.08rem;
            margin-top: 0.52rem;
            img{
                width:0.6rem;
                height:0.36rem;
            }
           
        }
        .text{
             p{
                padding-top:1rem;
                padding-left:2.08rem;
                font-size: 0.24rem;
                color:rgb(255,255,255);
                font-weight:200;
                line-height:0.24rem;
            }
            img{
                position: absolute;
                width:0.32rem;
                height: 0.32rem;
                padding-top:1.4rem;
                padding-left:2.08rem;
            }
            .txt{
                font-size: 0.2rem;
                padding-top:1.44rem;
                padding-left:2.48rem;
                color:rgb(255,255,255);
                font-weight:200;
                line-height: 0.24rem;
                
            }
        }
        .topPhoto4{
            position: absolute;
            width:0.85rem;
            height: 0.48rem;
            border-radius: 0.28rem;
            background-color:rgba(0,0,0,0.2);
            font-weight: 200;
            left:6.3rem;
            top:1.3rem;

            .five{
                font-size: 0.2rem;
                font-weight:200;
                line-height: 0.24rem;
                color:rgb(255,255,255);
                padding-left:0.15rem;
                margin-top:-0.42rem;
            }  
        }
        .topPhoto5{
            width:100%;
            height: 0.56rem;
            position: relative;
            margin-top:2.12rem;
            background-color: rgba(7,17,27,0.2);
            img{
                width:0.44rem;
                height: 0.24rem;
                padding-top:0.15rem;
                padding-left:0.24rem;
            }
            p{
                font-size: 0.1rem;
                color:rgba(255,255,255,255);
                font-weight: 200;
                line-height: 0.56rem;
                padding-left: 0.72rem;
                margin-top:-0.98rem;
                display: hidden;
            }
        }
    } 
</style>




